<template>
  <transition name='tantan'>
    <div id="editor">
      <mavon-editor style="height: 100%" v-model="value"></mavon-editor>
      <button class="btn btn-success" @click='upload_event'>点击提交</button>
      <button class="btn btn-danger" @click='stopedit'>结束编辑</button>
    </div>
  </transition>
</template>

<script>
  // Local Registration
  import {
    mavonEditor
  } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  export default {
    name: 'editor',
    data () {
      return {
        value: '',
        event_location: {},
        event_title: ''
      }
    },
    mounted: function () {
      if (!this.$store.state.editing) {
        this.$swal({
          type: 'error',
          title: '错误',
          text: '尚未进入编辑状态，请通过点击骚东西来编辑'
        }).then(() => {
          this.$router.push('/')
          this.$destroy() // 清除自身的一切数据 此处会弹出控制台警告  不过没影响
        }, () => {
          this.$router.push('/')
          this.$destroy() // 清除自身的一切数据 此处会弹出控制台警告  不过没影响
        })
      }
      this.event_location = this.$route.params.location
      this.event_title = this.$route.params.title
    },
    computed: {
      ajax_data () {
        const type = this.$store.state.opt.params.type
        if (type === 'EVENT_UPLOAD') {
          return {
            title: this.title,
            event: this.value,
            happentime: '2012-12-03 03:23:13',
            positionx: this.event_location.lng,
            positiony: this.event_location.lat
          }
        }
      }
    },
    components: {
      mavonEditor
      // or 'mavon-editor': mavonEditor
    },
    methods: {
      upload_event () {
        if (this.location === {}) {
          this.$swal('事件的地点还没选择')
          this.$router.push('/')
        } else {
          console.log(this.event_location)
          console.log(this.event_title)
        }
      },
      stopedit () {
        this.$swal({
          title: '危险',
          text: '你确定要停止编辑吗？你的一切改动将不会被保存',
          type: 'error',
          allowOutsideClick: false,
          allowEscapeKey: false,
          showCancelButton: true,
          confirmButtonText: '真的不想写了！',
          cancelButtonText: '好吧继续编辑',
          reverseButtons: true,
          focusCancel: true
        }).then(() => {
          this.$store.commit('stopedit')
          this.$router.push('/')
          this.$destroy() // 清除自身的一切数据
        }).catch(this.$swal.noop)
      }
    }
  }
</script>

<style>
  #editor {
    position: absolute;
    top: 100px;
    left: 60px;
    right: 60px;
    bottom: 100px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    z-index: 0
  }
  .tantan-enter-active {
    animation: tantan-in .5s ease;
  }
  .tantan-leave-active {
    animation: tantan-out .5s ease;
  }
  @keyframes tantan-in {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  @keyframes tantan-out {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(100%);
    }
  }
</style>



