<template>
<transition name='aa'>
  <div>
    <nav class='nav nav-default navbar-fixed-top maptab col-md-4 col-md-offset-4'>
      <div class='container'>
      <div class="navbar-header">
        <button id='menu-btn' class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-2" aria-expanded="false">
                        <tag></tag>
                      </button>
                      </div>
        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-2">
          <ul class="nav nav-tabs">
            <li @click='enter(pos.xinxiao)'>
              <tag txt='新校区'></tag>
            </li>
            <li @click='enter(pos.nanxiao)'>
              <tag txt='南校区'></tag>
            </li>
            <li @click='enter(pos.benbu)'>
              <tag txt='校本部'></tag>
            </li>
            <li @click='enter(pos.xiangya)'>
              <tag txt='湘雅'></tag>
            </li>
            <li @click='enter(pos.tiedao)'>
              <tag txt='铁道'></tag>
            </li>
            <div class='col-md-1'></div>
            <li @click='create_new_event'>
              <tag txt='发布我的骚东西'></tag>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <baidu-map class='bm' :center='position.center' @moving='sync' :scroll-wheel-zoom='scroll' :zoom='position.zoom'>
    <bm-navigation anchor="BMAP_ANCHOR_BOTTOM_LEFT"></bm-navigation>
    <bm-marker v-if='centerlabel' 
    :icon="{url: './static/img/arrow.ico', size: {width: 64, height: 64}}" 
    :position="position.center" 
    :dragging='dragging' 
    animation="BMAP_ANIMATION_DROP" 
    @rightclick='get_pos'>
    <bm-info-window :show='tips'>拖动我选择地点，右键点我确认</bm-info-window>
    </bm-marker>
      <input type='number' v-model="position.center.lng">
      <input type='number' v-model="position.center.lat">
      <input type='number' v-model="position.zoom">
    </baidu-map>
  </div>
  </transition>
</template>

<script>
  import tag from '@/components/tag'
  export default {
    data () {
      return {
        position: {// 点击定位一次后若鼠标拖动缩放   再点击相同地点  会出现错误
          center: {
            lng: 112.942658,
            lat: 28.164901
          },
          zoom: 15
        },
        dragging: true,
        scroll: true,
        centerlabel: false,
        tips: true,
        event_title: '',
        pos: {
          nanxiao: {
            lng: 112.942658,
            lat: 28.164901
          },
          xinxiao: {
            lng: 112.94644,
            lat: 28.154619
          },
          benbu: {
            lng: 112.935229,
            lat: 28.177018
          },
          xiangya: {
            lng: 112.949436, // 湘雅和铁道位置还没找!
            lat: 28.225955
          },
          tiedao: {
            lng: 112.999247,
            lat: 28.144036
          }
        }
      }
    },
    computed: {
      editing () {
        return this.$store.state.editing
      }
    },
    methods: {
      handler ({
        BMap,
        map
      }) {
        console.log(BMap, map)
        this.lng = 116.404
        this.lat = 39.915
      },
      sync (e) {
        this.position.center = e.target.getCenter()
      },
      enter (target) {
        setTimeout(() => {
          this.position.center = target
        }, 100)
        setTimeout(() => {
          this.position.zoom = 17.9
        }, 100)
        setTimeout(() => {
          this.position.zoom = 18
        }, 100)
      },
      create_new_event () {
        if (!this.editing) {
          this.$swal.queue([{
            progressSteps: ['1', '2'],
            title: '第一步',
            text: '请输入事件标题',
            confirmButtonText: '下一步',
            input: 'text',
            preConfirm: (title) => {
              return new Promise((resolve, reject) => {
                if (title === '') {
                  reject('标题不能为空！')
                } else {
                  resolve() // 默认参数有 input
                }
              })
            }
          }, {
            progressSteps: ['1', '2'],
            title: '第二步',
            text: '请在下面的地图中选取事件发生的地点',
            confirmButtonText: '去选取'
          }]).then((data) => {
            console.log(data)
            this.centerlabel = true
            this.event_title = data[0]
            this.$store.commit('startedit')
            setTimeout(() => {
              this.tips = false
            }, 3000)
          }).catch(this.$swal.noop)
        } else if (this.centerlabel === true) { // 当开始选择地点又还没右键确定的时候如果再点击发布
          this.$swal({
            type: 'warning',
            text: '请在下面的地图中选取事件发生的地点',
            confirmButtonText: '确定'
          })
        } else {
          this.$router.push('edit')
        }
      },
      get_pos (event) {
        this.$router.push({
          name: 'edit', // 只能用name来传params，如果用path数据就传不过去  也不知道为什么。
          params: {
            location: event.point,
            title: this.event_title
          }
        })
        this.centerlabel = false
        this.tips = true
      }
    },
    components: {
      tag
    }
  }
</script>

<style>
  .aa-enter-active {
    animation: aa-in .5s ease;
  }
  .aa-leave-active {
    animation: aa-out .5s ease;
  }
  @keyframes aa-in {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }
  @keyframes aa-out {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  .bm {
    position: absolute;
    top: 100px;
    left: 60px;
    right: 60px;
    bottom: 60px;
    box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2);
    -webkit-box-shadow: 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12), 0 2px 4px -1px rgba(0, 0, 0, .2)
  }
  .maptab {
    position: absolute;
    top: 61px
  }
  #menu-btn {
    bottom: 23px;
    left: 26px
  }
</style>



